body {
  margin: 0;
  padding: .5rem;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.main-div-header {
  border: .2rem solid #dbd8d8;
  border-radius: .5rem;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 1rem;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
}

.main-div-header .logo {
  color: indigo;
  cursor: pointer;
  font-size: 1rem;
}

.main-div-header nav a {
  margin: .2rem;
  padding: .2rem;
  font-size: larger;
  text-decoration: none;
}

.main-div-header nav a:hover {
  background-color: #f3f2f2;
  border-radius: .2rem;
}

.parent_div_card {
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.routepath {
  color: #000;
  text-decoration: none;
  display: flex;
}

.main-div-card {
  box-sizing: border-box;
  background-color: #fff;
  border: .2rem solid #dbd8d8;
  border-radius: .5rem;
  margin: 1rem;
  padding: 1rem;
}

.main-div-card:hover {
  cursor: pointer;
  border-color: #918a8a;
  transition: all .1s ease-in-out;
  transform: scale(1.01);
  box-shadow: .3rem .3rem 1rem #c0bcbc;
}

.main-div-card .food-image {
  box-sizing: border-box;
  width: 100%;
}

.main-div-card .food-image img {
  border-radius: .2rem;
}

.main-div-card .cuisines {
  font-size: .8rem;
}

.main-div-card .more-info {
  justify-content: space-between;
  align-items: center;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: .8rem;
  display: flex;
}

.main-div-card .more-info em {
  color: #fff;
  background-color: green;
  border-radius: .3rem;
  padding: .1rem;
}

.main-div-card .more-info .distance, .main-div-card .more-info .double-offer {
  text-transform: uppercase;
}

.main-card-restaurant-details {
  width: 60%;
  border-radius: .2rem;
  margin: auto;
  padding: 2rem;
}

.main-card-restaurant-details .restaurant_image {
  width: 60%;
  margin: auto;
}

.main-card-restaurant-details .restaurant_image img {
  border-radius: .2rem;
  box-shadow: 2px 2px 4px #000;
}

.main-card-restaurant-details .two_div_parent {
  border-bottom: 1px dotted #b7b7b7;
  margin-top: 1rem;
}

.main-card-restaurant-details .two_div_parent .basic_info {
  background-color: #f9f9f9;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
}

.main-card-restaurant-details .two_div_parent .basic_info .right_div-restaurant-details {
  border: 1px solid gray;
  border-radius: .5rem;
}

.main-card-restaurant-details .two_div_parent .basic_info .right_div-restaurant-details h4 {
  color: green;
  border-bottom: 1px solid gray;
  overflow: hidden;
}

.main-card-restaurant-details .two_div_parent .basic_info .right_div-restaurant-details p {
  color: gray;
  font-weight: bold;
}

.main-card-restaurant-details .two_div_parent .basic_info .right_div-restaurant-details h4, .main-card-restaurant-details .two_div_parent .basic_info .right_div-restaurant-details p {
  margin: 0;
  padding: .4rem;
}

.main-card-restaurant-details .two_div_parent .basic_offer_div {
  background-color: #f9f9f9;
  padding: 1rem;
}

.main-card-restaurant-details .address {
  background-color: #f9f9f9;
  margin-top: 1rem;
  padding: 1rem;
}

.all_shimmer_parent {
  grid-gap: 1rem;
  width: 100%;
  box-sizing: border-box;
  grid-template-columns: repeat(5, 1fr);
  display: grid;
}

.shimmer_main_div {
  width: 100%;
  box-sizing: border-box;
  border: .2rem solid #dbd8d8;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: .5rem;
  display: flex;
}

.shimmer_main_div .shimmer_image {
  height: 10rem;
  width: 100%;
  background-color: #cbc7c7;
  border-radius: .2rem;
  margin: .5rem;
}

.shimmer_main_div .shimmer_title {
  height: 1.5rem;
  width: 100%;
  background-color: #cbc7c7;
  border-radius: .2rem;
  margin: .5rem;
}

.shimmer_main_div .shimmer_rating {
  height: 1.5rem;
  width: 55%;
  background-color: #cbc7c7;
  border-radius: .2rem;
  margin: .5rem;
}

.footer {
  color: #fff;
  background-color: #000;
  padding: .8rem;
}

@media screen and (width <= 1100px) {
  .parent_div_card {
    grid-template-columns: repeat(3, 1fr);
  }

  .all_shimmer_parent {
    grid-template-columns: repeat(4, 1fr);
  }

  .all_shimmer_parent .shimmer_main_div {
    width: 100%;
  }
}

@media screen and (width <= 840px) {
  .parent_div_card {
    grid-template-columns: repeat(2, 1fr);
  }

  .all_shimmer_parent {
    grid-template-columns: repeat(3, 1fr);
  }

  .main-card-restaurant-details, .restaurant_image {
    width: 80%;
  }
}

@media screen and (width <= 576px) {
  .main-div-card {
    border: .1rem solid #dbd8d8;
    flex-direction: column;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
    align-items: flex-start;
    margin: .3rem;
    padding: .3rem;
    font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
  }

  .main-div-card:hover {
    box-shadow: .1rem .1rem .5rem #c0bcbc;
  }

  .main-div-card .resturant_name {
    font-size: .8rem;
  }

  .main-div-card .more-info {
    display: block;
  }

  .main-div-card .more-info .distance, .main-div-card .more-info .double-offer, .main-div-card .more-info .rating {
    margin: 2px;
  }

  .main-card-restaurant-details {
    width: 100%;
    box-sizing: border-box;
    padding: 1rem;
  }

  .main-card-restaurant-details .restaurant_image {
    width: 90%;
  }

  .all_shimmer_parent {
    grid-template-columns: repeat(2, 1fr);
  }

  .main-div-header {
    display: block;
  }

  .main-div-header .logo h2 {
    text-align: center;
    margin: 0;
  }

  .main-div-header nav {
    justify-content: center;
    align-items: center;
    font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    overflow: auto;
  }

  .main-div-header nav a {
    font-size: .9rem;
  }

  .shimmer_main_div {
    width: 9rem;
  }
}

